// 
import React, { useEffect, useState, useRef } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { useParams, useNavigate } from 'react-router-dom'

// 
import styled from 'styled-components'
import Header from '../components/Header'
import ButtonSubmit from '../components/ButtonSubmit'

// 
import { __getPlan, __patchCheckList } from '../redux/modules/PlanSlice'


const CheckList = () => {
    const dispatch = useDispatch();
    const { plans } = useSelector((state) => state.plans)
    const { me } = useSelector((state) => state.user)
    const { id } = useParams()
    const navigate = useNavigate();
    const [isChecked, setIsChecked] = useState(false) //  
    const [checkedItems, setCheckedItems] = useState([])//  
    const name = '';
    const content = useRef();
    const findCheckList = plans.find((item) => item._id === id)
    //useEffect 
    useEffect(() => {
        dispatch(__getPlan())
    }, [dispatch]);

    useEffect(() => {
        if (findCheckList) {
            const newArr = findCheckList?.checkList
            setCheckedItems([...newArr])
        }
    }, [findCheckList])

    useEffect(()=>{
        if(!me){
            navigate('/login')
        }
    },[me, navigate])
    //     

    //  
    const ListData = [
        {
            title: " ",
            menu: [
                { id: 1, name: "  "},
                { id: 2, name: "  (, ,  )"},
                { id: 3, name: "   "},

            ]
        },
        {
            title: " 1",
            menu: [
                { id: 4, name: " "},
                { id: 5, name: "/   " },
                { id: 6, name: ", , , ,   " },
                { id: 7, name: ", ,     , " },
                { id: 8, name: "     " },
                { id: 9, name: "/   " },
                { id: 10, name: "     " },

            ]
        },
        {
            title: " 3",
            menu: [
                { id: 11, name: "     " },
                { id: 12, name: "  ( 9)" },
                { id: 13, name: "   (50L 1~2)" },
                { id: 14, name: ",  " },
                { id: 15, name: "   " },
            ]
        },
        {
            title: " ",
            menu: [
                { id: 16, name: "   " },
                { id: 17, name: " A/S " },
                { id: 18, name: "    " },
                { id: 19, name: "  " },
                { id: 20, name: "       " },
                { id: 21, name: "    " },
            ]
        },
        {
            title: " ",
            menu: [
                { id: 22, name: "  " },
                { id: 23, name: ", ,    " },
                { id: 24, name: "  " },
                { id: 25, name: "   " },
                { id: 26, name: "    " },
                { id: 27, name: ", ,    " },
                { id: 28, name: "  " },
                { id: 29, name: "  " },
            ]
        }
    ]

    // 
    const checkHandler = (e) => {
        setIsChecked(!isChecked)
        checkedItemHandler(e.target.value, e.target.checked)
    }

    const checkedItemHandler = (id, isChecked) => {
        if (isChecked) { //  
            checkedItems.push(id) //  
            setCheckedItems(checkedItems)
        } else if (!isChecked && checkedItems.includes(id)) {
            setCheckedItems(checkedItems.filter((item) => item !== id))
        }
        return checkedItems;
    }

    const onSubmit = () => {
        let newArr = checkedItems.filter((item) => { return item !== undefined })
        console.log(newArr)
        dispatch(__patchCheckList({
            _id: findCheckList._id,
            checkList: newArr,
        }))
        navigate(-1)
    }

    const openHandler = (e) => {
        e.currentTarget.nextElementSibling.classList.toggle('open')
    }
    return (
        <>
            <Header />
            <Wrap>
                <TextBox>
                    <span></span>
                    <h1>
                        ,<br />
                          
                    </h1>
                </TextBox>
                {ListData.map((item, index) => (
                    <ListWrap key={index}>
                        <ListTitle onClick={openHandler}>
                            {item.title}
                            <img src={`${process.env.PUBLIC_URL}/images/toggleArrow.png`} alt="" />
                        </ListTitle>
                        <SubMenu ref={content}>
                            {item.menu.map((child) => (
                                <label key={child.id}>
                                    <input
                                        type="checkbox"
                                        checked={checkedItems.includes(child.name) ? true : false}
                                        value={child.name}
                                        onChange={(e) => checkHandler(e)}
                                    />
                                    <div>
                                        <h4>{child.name}</h4>
                                    </div>
                                </label>
                            ))}
                        </SubMenu>
                    </ListWrap>
                ))}
                <div onClick={onSubmit} style={{ marginTop: "auto", marginBottom: "24px" }}>
                    <ButtonSubmit buttonName={name}></ButtonSubmit>
                </div>
            </Wrap>
        </>
    )
}

export default CheckList

const Wrap = styled.div`
    width: calc(100% - 48px);
    height: 100%;
    box-sizing: border-box;
    overflow: scroll;
    position:relative;
    display: flex;
    flex-direction: column;
    margin: 0 auto;
  &::-webkit-scrollbar {
    display: none; /* , , ,  */
  }
`;

const TextBox = styled.div`
  padding-top: 86px;
  width: 100%;
  margin: 0 auto;
  margin-bottom: 19px;
  span{
    font-size: 14px;
    line-height: 25px;
    letter-spacing: 0.5px;
    color: #666666;
  }
  
  h1{
    font-size: 26px;
    font-style: normal;
    font-weight: 700;
    line-height: 40px;
    /* or 154% */
    letter-spacing: 0.5px;
    color: #282B49;
  }
`

const ListWrap = styled.ul`
    padding: 20px 0px;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    letter-spacing: -0.165px;
    color: #666666;
    border-bottom: 0.5px solid #BCBCBC;
`

const ListTitle = styled.li`
    display: block;
    cursor: pointer;
    display: flex;
    justify-content: space-between;
    align-items: center;
    img{
        display: block;
        width: 9px;
    }
`

const SubMenu = styled.div`
    max-height: 0;
    overflow:hidden;
    transition: max-height .3s ease;
    &.open{
        max-height: 500px;
    }
    > label {
        display: flex;
        padding: 20px 0px;

        input[type="checkbox"]{
            border: 0;
        }

        > div {
            margin-left: 22px;

            > h4{
                font-weight: 300;
                font-size: 13px;
                line-height: 18px;
                letter-spacing: -0.165px;
                color: #666666;
            }

            > p {
                font-weight: 400;
                font-size: 11px;
                line-height: 15px;
                letter-spacing: -0.165px;
                color: #999999;
            }
        }
    }
`